import React, { Component } from 'react';
import axios from 'axios';
import Dropzone from 'react-dropzone';
import './dropzone-override.css';
import LoadingSpinner from './LoadingSpinner';

import {Alert} from 'react-bootstrap';

class Form extends Component {
   constructor(props) {
    super(props);
    this.state = { files: [],
                  loading: false, // will be true when ajax request is running
                  alert: false,
                  response: "",
                  filterName:""
      };
   }
      
	 static defaultProps = {
    filters: ['Wave', 'Sunny', 'Sandy']
  }
onDrop(files) {
    this.setState({
      files
    });
  }

  handleSubmit(e) {
    console.log(this.refs.filter.value);
   this.setState({filterName: this.refs.filter.value});
   const data = new FormData();
   data.append('contentImage', this.state.files[0]);
   data.append('filterName', this.refs.filter.value);
   this.setState({ loading: true }, () => {
   axios.post('localhost:8080/demo/uploadImage?userId=admin', data)
   .then(resp => {this.setState({
         loading: false,
         alert: true,
         files: [],
         modelName:"",
         response: resp.data}, function() {console.log(resp.data);}
          )})
  });
    e.preventDefault(); 

}
  handleDismiss() {
    this.setState({ alert: false });
  }


	render() {
		let filterOptions = this.props.filters.map(filter => {
      return <option key={filter} value={filter}>{filter}</option>
    });
    const {loading, alert, response } = this.state;

		return(
      <div className="SelectUseCase">
        {alert? <div className="pull-right">{JSON.stringify(response.success)==='true'? <Alert bsStyle="success"  onDismiss={this.handleDismiss.bind(this)}>{JSON.stringify(response.msg)}</Alert>:<Alert bsStyle="danger" onDismiss={this.handleDismiss.bind(this)}>{JSON.stringify(response.msg)}</Alert>}</div>:<span></span>}
        <br/>
        <h3>Neural Style Image uploader </h3>
         {loading ? <LoadingSpinner />:<div></div>}
      <div className="row">

        <div className="col-sm-4">
        <h3>Step 1.</h3>
          <div className="form-group">
            <label for="filter"> <h4>Select Filter:</h4> </label> 
            <select className="form-control" ref="filter">
                      {filterOptions}
                  </select>
          </div>
        </div>

         <div className="col-sm-2"></div>
         <div className="col-sm-4">
        <div className="dropzone">
        <h3> Step 2. Upload Your Image/ Video:</h3> 
          <Dropzone onDrop={this.onDrop.bind(this)} className=" text-center dropzone-override">
            <h4>Try dropping a file here  <br/><br/>
            <i className="push-down fa fa-upload fa-lg"/> <br/><br/>
           or click to upload file </h4><br/>
           <h4>Dropped file:</h4>
          <ul>
            {
              this.state.files.map(f => <li key={f.name}>{f.name} - {f.size} bytes</li>)
            }
          </ul>
          </Dropzone>
        </div>
			</div>
      </div>
      <br/>
      <div className="row ">
      <div className="col-sm-3 pull-right">
        <button className="btn btn-primary" type="submit" onClick={this.handleSubmit.bind(this)}> Submit </button>  
      </div>
      </div>
      </div>
		);
	}
}

export default Form;


